* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 8px;
}

img {
  max-width: 100%;
  display: block;
}

.header {
  height: 100px;
  border-bottom: 2px solid #212121;
  padding-top: 12px;
}

.header-container {
  width: 500px;
  height: 70px;
  background-color: #a9a9a9;
  border-radius: 8px;
  margin: auto;
  padding-top: 20px;
}

.search-form {
  justify-content: center;
  display: flex;
}

.search-form__input {
  height: 30px;
  border: none;
  border-radius: 8px;
  outline-color: #0066fa;
  padding-left: 8px;
}

.search-form__button {
  color: #fff;
  width: 80px;
  height: 30px;
  background: #0066fa;
  border: none;
  border-radius: 8px;
  margin-left: 15px;
}

.gallery {
  flex-wrap: wrap;
  gap: 20px;
  margin-top: 15px;
  display: flex;
}

.photo-card {
  background-color: #a9a9a9;
  border: 2px solid #000;
  border-radius: 8px;
  flex-basis: calc(25% - 15px);
}

.info {
  justify-content: space-around;
  padding: 8px;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  margin-top: 0;
  display: flex;
}

.load-more {
  width: 120px;
  height: 40px;
  color: #fff;
  cursor: pointer;
  background: #0066fa;
  border: none;
  border-radius: 8px;
  margin-top: 15px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.load-more:disabled {
  color: #fff;
  cursor: not-allowed;
  background: #c1c1c1;
}

.load-more.hidden {
  display: none;
}

